import { prisma } from '@/lib/prisma';
import { MapPin, Calendar, Target, ChevronLeft, Clock, Share2, Download } from 'lucide-react';
import Link from 'next/link';
import { Card, Button, Badge } from '@/components/ui';

export default async function ScanReportPage({ params }: { params: Promise<{ id: string }> }) {
    const { id } = await params;

    const scan = await prisma.scan.findUnique({
        where: { id },
        include: { results: true },
    });

    if (!scan) return <div className="p-8 text-white">Scan not found</div>;

    const totalPoints = scan.gridSize * scan.gridSize;
    const completedPoints = scan.results.length;
    const progress = Math.round((completedPoints / totalPoints) * 100);

    return (
        <div className="p-8 max-w-7xl mx-auto space-y-8">
            {/* Header */}
            <header>
                <div className="flex items-center gap-2 mb-6">
                    <Link
                        href="/scans"
                        className="flex items-center gap-2 text-zinc-400 hover:text-white transition-colors"
                    >
                        <ChevronLeft size={18} />
                        Back to Scans
                    </Link>
                </div>

                <div className="flex flex-col md:flex-row justify-between items-start md:items-end gap-6">
                    <div>
                        <div className="flex items-center gap-3 mb-2">
                            <h1 className="text-4xl font-bold text-white">"{scan.keyword}"</h1>
                            <Badge color={scan.status === 'COMPLETED' ? 'green' : 'blue'}>{scan.status}</Badge>
                        </div>
                        <div className="flex flex-wrap gap-6 text-zinc-400 text-sm">
                            <span className="flex items-center gap-2">
                                <MapPin size={16} /> Lat: {scan.centerLat.toFixed(4)}, Lng: {scan.centerLng.toFixed(4)}
                            </span>
                            <span className="flex items-center gap-2">
                                <Target size={16} /> {scan.gridSize}x{scan.gridSize} ({scan.radius}km)
                            </span>
                            <span className="flex items-center gap-2">
                                <Calendar size={16} /> {new Date(scan.createdAt).toLocaleString()}
                            </span>
                            <span className="flex items-center gap-2">
                                <Clock size={16} /> {scan.frequency}
                            </span>
                        </div>
                    </div>

                    <div className="flex items-center gap-3">
                        <Button variant="outline"><Share2 size={18} /> Share</Button>
                        <Button variant="outline"><Download size={18} /> Export</Button>
                    </div>
                </div>
            </header>

            {/* Progress Bar (if running) */}
            {scan.status === 'RUNNING' && (
                <Card className="bg-blue-900/10 border-blue-500/20">
                    <div className="flex justify-between items-center mb-2">
                        <span className="text-blue-400 font-semibold text-sm animate-pulse">Scanning in progress...</span>
                        <span className="text-white font-mono">{progress}%</span>
                    </div>
                    <div className="w-full bg-zinc-800 rounded-full h-2">
                        <div
                            className="bg-blue-500 h-2 rounded-full transition-all duration-500 ease-out"
                            style={{ width: `${progress}%` }}
                        />
                    </div>
                </Card>
            )}

            {/* Main Grid View */}
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-8 h-[600px]">
                {/* Map / Grid Container */}
                <div className="lg:col-span-2 h-full">
                    <Card className="h-full flex items-center justify-center bg-zinc-950 relative overflow-hidden">
                        {/* Background "Map" Texture (Placeholder for real map) */}
                        <div className="absolute inset-0 opacity-10 bg-[url('https://upload.wikimedia.org/wikipedia/commons/e/ec/World_map_blank_without_borders.svg')] bg-cover bg-center" />

                        <div
                            className="grid gap-4 relative z-10"
                            style={{
                                gridTemplateColumns: `repeat(${scan.gridSize}, minmax(0, 1fr))`,
                            }}
                        >
                            {Array.from({ length: totalPoints }).map((_, i) => {
                                const result = scan.results[i];
                                // Rank Color Logic
                                // 1-3: Green, 4-10: Yellow, 10+: Red, Null: Gray
                                const rank = result?.rank; // Note: schema says rank is on Result, but we store JSON in topResults. 
                                // Actually, we store rank in the Result model too? Yes, `rank Int?`.

                                let bgClass = "bg-zinc-800 text-zinc-600 border-zinc-700";
                                if (result) {
                                    if (rank && rank <= 3) bgClass = "bg-emerald-500 text-white border-emerald-400 shadow-[0_0_15px_rgba(16,185,129,0.5)]";
                                    else if (rank && rank <= 10) bgClass = "bg-yellow-500 text-white border-yellow-400";
                                    else bgClass = "bg-red-500 text-white border-red-400";
                                } else if (scan.status === 'RUNNING' && i === completedPoints) {
                                    bgClass = "bg-blue-500/50 text-blue-200 border-blue-400 animate-pulse";
                                }

                                return (
                                    <div
                                        key={i}
                                        className={`w-12 h-12 md:w-16 md:h-16 rounded-full border-2 flex items-center justify-center font-bold text-lg md:text-xl transition-all transform hover:scale-110 cursor-pointer ${bgClass}`}
                                        title={result ? `Rank: ${rank}` : 'Pending'}
                                    >
                                        {result ? (rank || '-') : (scan.status === 'RUNNING' && i === completedPoints ? '...' : i + 1)}
                                    </div>
                                );
                            })}
                        </div>
                    </Card>
                </div>

                {/* Stats Panel */}
                <div className="h-full space-y-4">
                    <Card className="p-6">
                        <h3 className="text-lg font-bold text-white mb-4">Rank Distribution</h3>
                        <div className="space-y-4">
                            {/* Placeholder Distribution Bars */}
                            <div className="space-y-1">
                                <div className="flex justify-between text-xs text-zinc-400">
                                    <span>Top 1-3 (Map Pack)</span>
                                    <span>{scan.results.filter(r => r.rank && r.rank <= 3).length}</span>
                                </div>
                                <div className="w-full bg-zinc-800 rounded-full h-1.5">
                                    <div className="bg-emerald-500 h-1.5 rounded-full" style={{ width: `${(scan.results.filter(r => r.rank && r.rank <= 3).length / (completedPoints || 1)) * 100}%` }} />
                                </div>
                            </div>
                            <div className="space-y-1">
                                <div className="flex justify-between text-xs text-zinc-400">
                                    <span>Top 4-10</span>
                                    <span>{scan.results.filter(r => r.rank && r.rank > 3 && r.rank <= 10).length}</span>
                                </div>
                                <div className="w-full bg-zinc-800 rounded-full h-1.5">
                                    <div className="bg-yellow-500 h-1.5 rounded-full" style={{ width: `${(scan.results.filter(r => r.rank && r.rank > 3 && r.rank <= 10).length / (completedPoints || 1)) * 100}%` }} />
                                </div>
                            </div>
                            <div className="space-y-1">
                                <div className="flex justify-between text-xs text-zinc-400">
                                    <span>10+ (Invisible)</span>
                                    <span>{scan.results.filter(r => r.rank && r.rank > 10).length}</span>
                                </div>
                                <div className="w-full bg-zinc-800 rounded-full h-1.5">
                                    <div className="bg-red-500 h-1.5 rounded-full" style={{ width: `${(scan.results.filter(r => r.rank && r.rank > 10).length / (completedPoints || 1)) * 100}%` }} />
                                </div>
                            </div>
                        </div>
                    </Card>

                    <Card className="p-6 flex-1">
                        <h3 className="text-lg font-bold text-white mb-4">Top Competitors</h3>
                        <p className="text-sm text-zinc-500 italic">
                            Competitor analysis module coming in v1.2
                        </p>
                    </Card>
                </div>
            </div>
        </div>
    );
}
